*,
*::after,
*::before {
  box-sizing: border-box; }

ul, li {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none; }

a {
  display: inline-block;
  text-decoration: none;
  color: inherit; }

.container {
  max-width: 1260px;
  padding: 0 30px;
  margin: 0 auto; }

body {
  font-family: "Rubik" ,sans-serif;
  font-size: 18px;
  font-weight: 300;
  color: #40394A;
  line-height: 25.3px;
  overflow-x: hidden; }

.header {
  height: 100vh; }

section {
  padding: 60px 0; }

h1, h2 {
  font-family: "Quicksand", sans-serif;
  font-size: 45px;
  font-weight: 400;
  line-height: 56px;
  margin: 0; }

h2 {
  font-size: 35px;
  line-height: 44px;
  margin: 0 0 12px 0; }

p {
  margin-top: 0; }

.menu {
  font-family: "Quicksand", sans-serif; }
  .menu li {
    display: inline-block; }
  .menu a {
    padding: 30px 15px;
    transition: all 0.3s; }
    .menu a:hover {
      color: #807293; }

.socials {
  cursor: pointer; }
  .socials .twitter {
    width: 14px; }
  .socials .facebook {
    width: 9px; }
  .socials .instagram {
    width: 13px; }
  .socials .linkedin {
    width: 14px; }
  .socials svg {
    fill: #7ECA9C;
    width: 100%;
    transition: all 0.3s; }
    .socials svg:hover {
      fill: #B2EEC9; }
  .socials a + a {
    margin-left: 25px; }

.btn {
  min-height: 40px;
  border: 1px solid #40394A;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  line-height: 21px;
  max-width: 145px;
  display: inline-block;
  transition: all 0.3s; }
  .btn:hover {
    background: #40394A;
    color: #fff; }
  .btn a {
    padding: 10px 22px; }

.card {
  width: 236px;
  height: 166px;
  box-shadow: 0px 4px 10px 10px rgba(0, 0, 0, 0.05);
  border-radius: 5px; }

.tag-btn {
  min-height: 40px;
  color: #939393;
  display: inline-block;
  padding: 10px;
  border: 1px solid #D2D2D2;
  border-radius: 5px;
  cursor: pointer; }

.mburger {
  display: none;
  --mb-bar-width: 0.4;
  --mb-bar-height: 2px;
  --mb-bar-spacing: 8px; }

.mburger-close {
  margin-right: auto; }

.header-top {
  box-shadow: 0px 16px 54px rgba(0, 0, 0, 0.08); }
  .header-top .container {
    min-height: 90px;
    display: flex;
    align-items: center;
    flex-wrap: wrap; }
  .header-top__logo {
    margin-right: 60px; }
  .header-top__menu {
    margin-right: 150px; }
  .header-top__socials {
    margin-right: 50px; }
  .header-top__search {
    margin-right: 25px; }
    .header-top__search svg {
      transition: all 0.3s; }
      .header-top__search svg:hover {
        fill: #B2EEC9; }
    .header-top__search svg {
      fill: #7ECA9C;
      width: 20px; }

.header-body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 85vh; }
  .header-body__info {
    max-width: 510px; }
  .header-body__title {
    margin: 0 0 16px 0; }
  .header-body__text {
    margin: 0 0 14px; }
  .header-body__img {
    padding: 0 36px 0 0; }
    .header-body__img img {
      height: 65vh; }

.tools {
  min-height: 80vh; }
  .tools .container {
    display: flex;
    align-items: center; }
  .tools__cards {
    max-width: 540px;
    width: 100%;
    display: flex;
    flex-wrap: wrap; }
    .tools__cards img {
      position: relative;
      vertical-align: top; }
    .tools__cards .card_design {
      margin: 5px 68px 0 0; }
    .tools__cards .card_backend {
